:root {
  --primary: #66ffcc;
  --secondary: #ff66cc;
  --body:  #66ccff;
}
body {
  margin: 0;
  padding: 0;
  background-color: var(--body);
  font-family: Indie Flower, Segoe UI, Helvetica, sans-serif;
}
.App {
  text-align: center;
}

.App-header {
  padding: 18px;
  text-align: center;
  color: var(--secondary);
  background-image: url(/ChicagoEats/static/media/logo.40ef7ce2.svg);
  background-color: var(--primary);
  background-repeat: repeat-x;
}

.App-title {
  margin: 0;
  font-size: 64px;
}

.App-intro {
  padding: 0 15%;
  font-size: 32px;
}
